import { Component, h, State, Event, EventEmitter } from '@stencil/core';

@Component({
    tag: 'send-document-button-group',
    shadow: true,
    styleUrl: 'send-document-button-group.scss',
})
export class SendDocumentButtonGroup {
    @Event() validateDocument: EventEmitter<boolean>;

    @State() private disabled: boolean = false;
    @State() private loading: boolean = false;

    constructor() {
        this.handleOnClickSendButton = this.handleOnClickSendButton.bind(this);
    }

    public render() {
        return [
            <div class="send-document-button-group">
                <limel-button
                    label="Prepare for sendout"
                    primary={true}
                    loading={this.loading}
                    disabled={this.disabled}
                    onClick={this.handleOnClickSendButton}
                />
            </div>,
        ];
    }

    private handleOnClickSendButton() {
        this.validateDocument.emit();
    }
}
